<template>
  <div style="height: 100vh">
    <router-link to="/">
      <van-button class="home-btn" round size="small">
        <van-icon name="shouye" class="iconfont" class-prefix="icon" />
      </van-button>
    </router-link>
    <iframe id="iframe" :src="src" scrolling="auto" frameborder="0">
      该网站使用了框架技术，但是您的浏览器不支持框架，请升级您的浏览器以便正常访问。
    </iframe>
  </div>
</template>

<script>
export default {
  name: 'WisdomCanteen',
  data() {
    return {
      src: ''
    }
  },
  props: {
    redirect_uri: {
      type: String,
      default: ''
    }
  },
  created() {
    const token = this.$ls.get('access_token')
    this.src = `${this.$util.WISDOM_CANTEEN_IP}/reserve/#/singleLogin?token=${token}${this.redirect_uri}`
  }
}
</script>
<style lang="less" scoped>
#iframe {
  width: 100%;
  height: 100%;
}
.home-btn {
  position: fixed;
  top: 50%;
  right: 0;
}
</style>